import React from 'react'
import Product from "../../Assets/Product.jpg"
import Products from '../Product/Products'
import ContactUs from '../ContactUs/Contactus'
import Header from '../Header/Header'
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import {motion}from "framer-motion"
import {useInView} from "react-intersection-observer"

function Body() {
  const {ref,inView}=useInView();
    return (
        <div className="">
          <Header />
         <div ref={ref} className=" px-10 py-16 flex justify-center">
          <div className="w-1/3 h-100 ">
            <motion.img
            animate={{x:inView?[-600,0]:0}}
            alt="Product " className="" src={Product}></motion.img></div> 
          
          <motion.div   animate={{x:inView?[600,0]:0}}
           className="ml-12"> 
           <h1 className="font-bold text-2xl">Pure Rose Essential Oil</h1>
           <p className='my-8 text-gray-800'>Rose Petal Extract, 5 ml</p>
          <span className='text-gray-500 p text-lg mr-4 line-through'>259.00DT</span>
            <span className="p text-red-500 text-2xl">159.00DT</span>
           <br></br>
       
<div className='flex mt-8 mb-6 px-4'>
<motion.button   whileHover={{scale:1.1}}
 className='w-full bg-black  p text-white border-2 border-white hover:bg-white p-3 hover:text-black hover:border-black'><a className=" p-3 " href="#Product">Shop Now</a></motion.button>
<div className='border  border-black py-2  flex items-center w-20  h-max'>
<motion.button   transition={{ repeat: Infinity, duration: 1 }} animate={{scale:[1,1.1,1.2,1.1,1]}}
 className="ml-3 p-2 "><FavoriteBorderIcon /></motion.button></div>
</div>
       
       
             <h1 className="font-bold mb-4">Description</h1>
          <p className="w-80 ">A soft, floral scent to calm the body and mind.A soft, floral scent to calm the body and mind.</p>
          <h1 className="font-bold mt-5 mb-2">Shipping</h1>
          <p className="">We ship our products worldwide, with a flat shipping rate.</p>
          </motion.div>

</div>



<Products />
<ContactUs />
<hr className="h1 "></hr>
      <div className="flex justify-center"> <h1 className=" text-center mt-10 mb-6 text-3xl w-[600px]">“My all time favourite scent. It smells divine and lasts all day! A true rose fragrance.”</h1>
   
  
      </div>    <p className="text-center   mb-20">Sarah from Melbourne</p>
        </div>
    )
}

export default Body
