import React, { useEffect ,useState} from 'react'
import { useSelector } from 'react-redux'
import { useDispatch } from 'react-redux';
import { RemoveFromCart ,DecreaseQuantite,IncreaseQuantite,SumAllInCart} from '../redux/CartSystem';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { DataArray } from '@mui/icons-material';
import {motion}from "framer-motion"
function Card() {
  const {carts}=useSelector(item=>item.user)
  
  const {sum}=useSelector(item=>item.user)
  const dispatch=useDispatch()
  const[data,setData]=useState(0)
  useEffect(()=>{
   dispatch(SumAllInCart() );
  
  },[carts])

  useEffect(()=>{
  localStorage.setItem("ShoppingCart",JSON.stringify(carts))
  
              
   
   },[carts]) 
   
    return (
        <div>
              <div class="h-max pb-20 bg-gray-100 pt-20">
    <h1 class="mb-10 text-center text-2xl font-bold">Cart Items</h1>


    {carts.length>0?
   
    <div class="mx-auto max-w-5xl justify-center px-6 md:flex md:space-x-6 xl:px-0">
      <div class="rounded-lg md:w-2/3">
                { 
                  carts.map(p=>{
           

                 
                    return(

                
        <div class="justify-between mb-6 rounded-lg bg-white p-6 shadow-md sm:flex sm:justify-start">
          <img src={p.src} alt={p.name+" Image"} class="w-full rounded-lg sm:w-40" />
          <div class="sm:ml-4 sm:flex sm:w-full sm:justify-between">
            <div class="mt-5 sm:mt-0">
              
<p className='mb-3 w-max ' id="brand"><a className="text-[12px]" href="https://www.linkedin.com/in/ismail-chebaane-555b75146/">CHEBAANE Ismail</a></p>

              <h2 class="text-lg font-bold text-gray-900">{p.name}</h2>
              <p class="mt-1 mb-4 text-xs text-gray-700">Item {p.id+""+p.price-200+""+p.price+""}</p>
         
              <ul class="flex justify-start mb-4">
  <li>
    <svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="star" class="w-4 text-black mr-1" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512">
      <path fill="currentColor" d="M259.3 17.8L194 150.2 47.9 171.5c-26.2 3.8-36.7 36.1-17.7 54.6l105.7 103-25 145.5c-4.5 26.3 23.2 46 46.4 33.7L288 439.6l130.7 68.7c23.2 12.2 50.9-7.4 46.4-33.7l-25-145.5 105.7-103c19-18.5 8.5-50.8-17.7-54.6L382 150.2 316.7 17.8c-11.7-23.6-45.6-23.9-57.4 0z"></path>
    </svg>
  </li>
  <li>
    <svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="star" class="w-4 text-black mr-1" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512">
      <path fill="currentColor" d="M259.3 17.8L194 150.2 47.9 171.5c-26.2 3.8-36.7 36.1-17.7 54.6l105.7 103-25 145.5c-4.5 26.3 23.2 46 46.4 33.7L288 439.6l130.7 68.7c23.2 12.2 50.9-7.4 46.4-33.7l-25-145.5 105.7-103c19-18.5 8.5-50.8-17.7-54.6L382 150.2 316.7 17.8c-11.7-23.6-45.6-23.9-57.4 0z"></path>
    </svg>
  </li>
  <li>
    <svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="star" class="w-4 text-black mr-1" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512">
      <path fill="currentColor" d="M259.3 17.8L194 150.2 47.9 171.5c-26.2 3.8-36.7 36.1-17.7 54.6l105.7 103-25 145.5c-4.5 26.3 23.2 46 46.4 33.7L288 439.6l130.7 68.7c23.2 12.2 50.9-7.4 46.4-33.7l-25-145.5 105.7-103c19-18.5 8.5-50.8-17.7-54.6L382 150.2 316.7 17.8c-11.7-23.6-45.6-23.9-57.4 0z"></path>
    </svg>
  </li>
  <li>
    <svg aria-hidden="true" focusable="false" data-prefix="far" data-icon="star" class="w-4 text-black mr-1" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512">
      <path fill="currentColor" d="M528.1 171.5L382 150.2 316.7 17.8c-11.7-23.6-45.6-23.9-57.4 0L194 150.2 47.9 171.5c-26.2 3.8-36.7 36.1-17.7 54.6l105.7 103-25 145.5c-4.5 26.3 23.2 46 46.4 33.7L288 439.6l130.7 68.7c23.2 12.2 50.9-7.4 46.4-33.7l-25-145.5 105.7-103c19-18.5 8.5-50.8-17.7-54.6zM388.6 312.3l23.7 138.4L288 385.4l-124.3 65.3 23.7-138.4-100.6-98 139-20.2 62.2-126 62.2 126 139 20.2-100.6 98z"></path>
    </svg>
  </li>
  <li>
    <svg aria-hidden="true" focusable="false" data-prefix="far" data-icon="star" class="w-4 text-black" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512">
      <path fill="currentColor" d="M528.1 171.5L382 150.2 316.7 17.8c-11.7-23.6-45.6-23.9-57.4 0L194 150.2 47.9 171.5c-26.2 3.8-36.7 36.1-17.7 54.6l105.7 103-25 145.5c-4.5 26.3 23.2 46 46.4 33.7L288 439.6l130.7 68.7c23.2 12.2 50.9-7.4 46.4-33.7l-25-145.5 105.7-103c19-18.5 8.5-50.8-17.7-54.6zM388.6 312.3l23.7 138.4L288 385.4l-124.3 65.3 23.7-138.4-100.6-98 139-20.2 62.2-126 62.2 126 139 20.2-100.6 98z"></path>
    </svg>
  </li>
</ul>
			
          
            </div>
            <div class="mt-4 flex justify-between im sm:space-y-6 sm:mt-0 sm:block sm:space-x-6">
            
              <div class="flex  justify-center items-center  border-gray-100">
                  <span onClick={()=>{dispatch(DecreaseQuantite(p))}} class="cursor-pointer rounded-l bg-gray-100 py-1 px-3.5 duration-100 hover:bg-blue-500  hover:text-blue-50">-  </span>
                  <input class="h-8 w-8 border bg-white text-center text-xs outline-none" type="number" value={p.quantity} min="1" />
                  <span onClick={()=>{dispatch(IncreaseQuantite(p))}} class="cursor-pointer rounded-r bg-gray-100 py-1 px-3 duration-100 hover:bg-blue-500 hover:text-blue-50"> + </span>
                </div>
            <div class="flex justify-end items-center space-x-4">
            
            <span className="text-md font-semibold   text-gray-900 dark:text-white">{p.price>400?<div><span className="text-gray-500  text-sm  line-through">{p.price+" DT"}</span><span className="p text-red-500"> - {+p.price-200} DT</span></div>:<span className='p'>{p.price +" DT"} </span>}</span>
                 <button onClick={()=>{dispatch(RemoveFromCart(p))}}>
                <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" class="h-5 w-5 cursor-pointer duration-150 hover:text-red-500">
                  <path stroke-linecap="round" stroke-linejoin="round" d="M6 18L18 6M6 6l12 12" />
                </svg></button>     
              </div>
             
             
            </div>
          </div>
        </div>

)

})  

    }


      </div>
      
      <div class="mt-6 h-full rounded-lg border bg-white p-6 shadow-md md:mt-0 md:w-1/3">
        <div class="mb-2 flex justify-between">
          <p class="text-gray-700">Subtotal</p>
          <p class="text-gray-700">
            {(sum.reduce((total,currentItem) =>  total = total + currentItem , 0 ))+" DT"
            }
         
            </p>
        </div>
        <div class="flex justify-between">
          <p class="text-gray-700">Shipping</p>
          <p class="text-gray-700">7.99 DT</p>
        </div>
        <hr class="my-4" />
        <div class="flex justify-between">
          <p class="text-lg font-bold">Total</p>
          <div class="">
            <p class="mb-1 text-lg font-bold">
            {(sum.reduce((total,currentItem) =>  total = total + currentItem , 0 ))+7.99+" DT"
            }
         

            </p>
            <p class="text-sm text-gray-700">including VAT</p>
          </div>
        </div>
        <motion.button  
         whileHover={{y:[0,-10,0]}}
         transition={{repeat:Infinity,duration:1}}
         class="mt-6 w-full rounded-md bg-black py-1.5 font-medium text-blue-50 hover:bg-gray-800">Check Out</motion.button>
      </div>
    </div>
     
     :<div ><h1 className="text-xl flex justify-center text-gray-400">Your cart currently Empty</h1>
     <a  className="text-xl flex mt-2 justify-center items-center text-gray-400" href="/"><ArrowBackIcon /> Start Shopping</a>
    </div>
    
    }
  </div>
        </div>
    )
}

export default Card
