import {React,useState} from 'react'
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import ProductCard from '../ProductCard/ProductCard'
import {prod} from "./ProductData"
import {prodm} from "./ProductData"
import {prodw} from "./ProductData"
import {prodb} from "./ProductData"
import {motion}from "framer-motion"
import {useInView} from "react-intersection-observer"
function Products() {
  const {ref,inView}=useInView();
      let [menclick,setmenclick]=useState(false);
      let [allclick,setallclick]=useState(false);
      let [womenclick,setwomenclick]=useState(false);
      let [babyclick,setbabyclick]=useState(false);
      let [resultclick,setresultclick]=useState(false);
      let [sortclick,setsortclick]=useState(false);
      
   


    return (
        <div id="Product">
           <div className='flex  justify-center items-center'><hr className=' w-80 pb-2 border-black'></hr>
              </div>
              <div className='flex  justify-center items-center'><hr className=' w-60 pb-2 border-black '></hr>
              </div>
              <div className='flex  justify-center items-center'><hr className=' w-40 pb-2 border-black '></hr>
              </div>
            <h2 className="my-10 text-center text-3xl">Our Products</h2>
            <div className='flex  justify-center items-center'><hr className=' w-40 pb-2 border-black '></hr>
              </div>
            <div className='flex  justify-center items-center'><hr className=' w-60 pb-2 border-black '></hr>
              </div>
            <div className='flex  justify-center items-center'><hr className=' w-80 border-black '></hr>
              </div>  
              
               <div className="flex justify-around mt-10 border "> 
               <button  className={allclick?"w-full border hover:shadow-xl py-4 h-full     font-bold p underline text-center":"w-full border hover:shadow-xl py-4 h-full     font-bold p text-center"  } onClick={()=>{setallclick(true); setresultclick(40);setmenclick(false);setwomenclick(false);setbabyclick(false) }}>All</button>
                <button  className={menclick?"w-full  border hover:shadow-xl py-4 h-full   font-bold p underline text-center":"w-full  border hover:shadow-xl py-4 h-full   font-bold p text-center"} onClick={()=>{setmenclick(true);setallclick(false) ;setwomenclick(false);setbabyclick(false) ;setresultclick(16)}}>Men</button>
                 <button  className={womenclick?"w-full  border hover:shadow-xl py-4 h-full  font-bold p text-center underline" :"w-full  border hover:shadow-xl py-4 h-full  font-bold p text-center" }onClick={()=>  {setmenclick(false);setallclick(false) ;setwomenclick(true);setbabyclick(false);setresultclick(14) }}>Women </button>
                  <button  className={babyclick?"w-full  border hover:shadow-xl py-4 h-full font-bold p text-center  underline "  :"w-full  border hover:shadow-xl py-4 h-full font-bold p text-center"  }onClick={()=>{setmenclick(false);setallclick(false) ;setwomenclick(false);setbabyclick(true) ;setresultclick(10)}}>Kids</button>
                   </div>
            <div className="flex justify-between pt-6 pl-16 pr-10  bg-slate-100">
            <span>{resultclick?(resultclick+" "):40+" "}Results</span> <div className="">
              <button className="hover:underline"  onClick={()=>{setsortclick(!sortclick)}}>  <p>Sort by : <strong>Relevance <KeyboardArrowDownIcon /> </strong></p>
           </button>
           <div className={sortclick?"h-max w-max bg-white absolute border":"hidden h-max w-max bg-white absolute border"}>
        <button className="p-3 flex items-center justify-center hover:shadow-2xl hover:underline">Price High to Low</button>
        <button className="p-3 flex items-center justify-center hover:shadow-2xl hover:underline">Price Low to High</button>
        <button className="p-3 flex items-center justify-center hover:shadow-2xl hover:underline">New Arrivals</button>
           </div>
              </div>  </div>
<div class=" md:grid-cols-6 lg:grid-cols-3 grid-cols-[auto-fit_minmax(100px,_1fr)] sm:pl-0 md:pl-0 pt-6 lg:pl-14 pb-28 grid  gap-4 bg-slate-100 mx-auto">

     
{
           
      
           prod.map((p,index)=>{
          if(menclick===true&&p.id<17){
           return(
         
     
                <ProductCard id={p.id} description={p.description} key={index} src={p.src} name={p.name} price={p.price}   />
            
               
                
                
                
            
                
            )
          
          }
          else   if(womenclick===true&&(p.id>16&&p.id<31)){

            return(
            
           
       
                  <ProductCard id={p.id} description={p.description} key={index} src={p.src} name={p.name} price={p.price}   />
              
                  
                  
                  
                 
                
                
                )  
          }
          else if(babyclick===true&&p.id>30){

            return(
         
         

          
           
       
                  <ProductCard id={p.id} description={p.description} key={index} src={p.src} name={p.name} price={p.price}   />
              
                  
                  
                  
                  
  
       
                
                
                
                )  
          }
          else if((babyclick===false&&menclick===false&&womenclick===false)||allclick===true){

            return(
         
     
                <ProductCard id={p.id} description={p.description} key={index} src={p.src} name={p.name} price={p.price}   />
            
                
                
                
                )  
          }
           
     
            })
        }



</div>
</div>
    )
}

export default Products
