import React from 'react'
import { NavLink ,Link } from 'react-router-dom'
import Headerimg from "../../Assets/Header.jpg"
import Logo from "../../Assets/logo/logo-color.png"
import NavBar from '../Navbar/NavBar'
import { TypeAnimation } from 'react-type-animation';
import {motion}from "framer-motion"
import {useInView} from "react-intersection-observer"

function Header() {
    return (
        <div>
    <NavBar />


       <div className=''> <img alt="HeaderImage" className="h-[500px] pt-[70px] imagerelative  w-full  " src={Headerimg}></img> 
        
     <div className="textinimage absolute top-40 left-40"><h1 className="text-4xl h1 font-bold mb-4 w-60 tracking-wide">
     <TypeAnimation
       sequence={[
        '100% Pure Rose',
        1000,
        '100% Essential Oil',
        
        1000,
        ]}
        speed={50} // Custom Speed from 1-99 - Default Speed: 40
        style={{ fontSize: '1em' }}
        wrapper="h1" // Animation will be rendered as a <span>
        repeat={Infinity} // Repeat this Animation Sequence infinitely
    
      cursor={true}

    
    />
     
</h1>
<p className="w-80 text-lg p mb-5">Our range of high-quality essential oils has been hand-picked by our scent specialists.</p>
      <motion.button   whileHover={{scale:1.2}}  className="border-2   font-bold border-black hover:bg-black p-4 hover:text-white hover:border-white"><a href="#Product" className='p-4 tracking-widest w-full h-full'>Shop Now</a></motion.button>
      
      </div>  
       </div>
        <div className="flex justify-center">
     <h1 className="text-3xl h1 text-center w-[700px]  my-14 ">Rose essential oil is known for its moisturizing, comforting, and balancing properties.</h1>
       
        </div>
        <hr className="h-1 " />    
        </div>
    )
}

export default Header
