import React from 'react'
import { NavLink ,Link } from 'react-router-dom'
import WorkOutlineIcon from '@mui/icons-material/WorkOutline';
import Logo from "../../Assets/logo/logo-color.png"
import AddCartImg from "../../Assets/add-to-cart.png"
import { useSelector } from 'react-redux'
import perfume from "../../Assets/logo/perfume.png"
import {motion}from "framer-motion"
function NavBar() {
  const {carts}=useSelector(item=>item.user)
    return (
        <div>
                   <div class="wrapper">
    <nav>
      <input type="checkbox" id="show-search" />
      <input type="checkbox" id="show-menu" />
      <label for="show-menu" class="menu-icon"><i class="fas fa-bars"></i></label>
      <div class="content">
      <div class="logo sm:mr-0 lg:mr-40"><Link className="flex" to="/"><img className="w-20 h-12 m" alt="logoImage" src={Logo}></img> 
      <motion.img   transition={{ repeat: Infinity, duration: 1 }} animate={{y:[0,-10,-20,-30,-20,-10,0,10 ,0]}} alt="HeaderImage" className="w-12 h-12  " src={perfume}></motion.img> 
  
      </Link></div>
        <ul class="links ">
          <li><Link to="/">Home</Link></li>
          <li><a href="#Product">Products </a></li>
          <li>
            <a href="#Product" class="desktop-link">Category</a>
            <input type="checkbox" id="show-features" />
            <label for="show-features">Category</label>
            <ul>
              <li><a href="#Product" >Men's Perfume</a></li>
              <li><a href="#Product" >Women's Perfume</a></li>
              <li><a href="#Product" >Kids's Perfume</a></li>
           
            </ul>
          </li>
          <li>
          <a href="#contact">Contact us</a>
          </li>
          <li><a href="#footer">About </a></li>
          
        </ul>
      </div>
      <Link to="/Cart" className='ml-40  relative'><Link to="/Cart"><img alt="CartImg" className="w-8 h-8" src={AddCartImg}></img> </Link>
          <span className="bg-white absolute rounded-full flex justify-center font-bold top-[-2px] left-[14px] text-black  w-6 h-6">{carts.length}</span>
           </Link>
      <label for="show-search" class="search-icon"><i class="fas fa-search"></i></label>
      <form action="#" class="search-box">
        <input type="text" placeholder="Type Something to Search..." required />
        <button type="submit" class="go-icon"><i class="fas fa-long-arrow-alt-right"></i></button>
      </form>
    </nav>
  </div>

        </div>
    )
}

export default NavBar
